import styled from "styled-components";

const Wrapper = styled.div`
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
  overflow: hidden;
  h1 {
    font-size: 30px;
    color: ${(props) => props.theme.accentColor};
    font-weight: 400;
  }
  p {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }
  span {
    margin-top: 15px;
  }
`;

const Rule = () => {
  return (
    <Wrapper>
      <h1></h1>
      <p>
        <span>1. 4X3 12   4  .</span>
        <span>2.     3   .</span>
        <span>3.       .</span>
        <span>
          4.          
             1 1   .
        </span>
        <span>
          5.          
                   . (
             .)
        </span>
        <span>6.  2.</span>
        <span>6.1   </span>
        <span>
          6.2           
           
        </span>
        <span>
          7. ()     () .
          ()         .
        </span>
        <span>
          8.  ()      ()
            .
        </span>
        <span></span>
      </p>
    </Wrapper>
  );
};

export default Rule;
